.solving-page_banner {
  width: 100%;
  height: 80px;
  background-color: rgb(40, 116, 237);
  opacity: 0.8;
  min-height: 50px;
  border-radius: 5px;
  position: relative;
  top: 20px;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;
}

.banner_text {
  color: white;
  font-size: 18px;
}


.solving-page_buttons {
  display: grid;
  grid-template-columns: 115px 115px 115px;
  grid-gap: 20px;
  justify-content: center;
  margin-top: 23px;
}

